'use strict';

import React from 'react';
import {Link} from 'react-router';

export default React.createClass({
   render(){
      return (
         <div>
            <div>
               <Link to="/"></Link>
               <Link to="/tv"></Link>
            </div>
            {this.props.children}
         </div>
      )
   }
})



